import React from "react";
import "./FullPage.css";
import { useSpring, animated } from "react-spring";
export default function FullPage(props) {
  const colors = {
    0: "#fe5f55",
    1: "#6A706E",
    2: "#28666E",
    3: "#1B1B3A",
  };

  const styles = useSpring({
    config: { duration: 600 },
    background: colors[props.current],
  });

  return (
    <animated.div className="script-box" style={styles}>
      {props.children}
    </animated.div>
  );
}
