import Ppt from "./Ppt";
function Rig() {
  return useFrame((state) => {
    state.camera.position.x = THREE.MathUtils.lerp(
      state.camera.position.x,
      1 + state.mouse.x / 2,
      0.075
    );
    state.camera.lookAt(0, 0.3, 0);
    // state.camera.position.y = THREE.MathUtils.lerp(
    //   state.camera.position.y,
    //   1.5 + state.mouse.y / 4,
    //   0.075
    // );
  });
}

