import React from "react";
import illustration from "./images/bolt.png";
import { useSpring, animated } from "react-spring";

export default function IlustrationComponent(props) {
  const styles = useSpring({
    config: { duration: 900, delay: 600 },
    opacity: props.toggle == 1 ? 1 : 0,
    marginLeft: props.toggle == 1 ? 0 : 100,
  });
  return (
    <>
      <animated.img src={illustration} style={styles}></animated.img>
    </>
  );
}
